<template>
  <div class="techo-layout-theme-header-user">
    <span class="techo-layout-theme-header-user-name">欢迎您，{{info.userName}}</span>
    <el-menu class="techo-layout-theme-header-user-menu" mode="horizontal">
      <el-submenu index="1">
        <template slot="title">
          <img class="techo-layout-theme-header-user-img" :src="imgUser">
        </template>
        <el-menu-item index="2-1" @click="handleLogout">注销</el-menu-item>
        <!-- <el-menu-item index="2-2" @click="handleUpdatePw">修改密码</el-menu-item> -->
        <!-- <el-menu-item index="2-3" @click="handlePersonalSetting">设置中心</el-menu-item> -->
      </el-submenu>
    </el-menu>
  </div>
</template>

<script>
  import {
    mapState,
    mapActions
  } from 'vuex'

  export default {
    data(){
      return {
        imgUser: require("@/assets/images/main/defavatar.png"),
      }
    },
    computed: {
      ...mapState('techo/user', [
        'info'
      ])
    },
    methods: {
      ...mapActions('techo/account', [
        'logout'
      ]),
      /**
       * @description 登出
       */
      handleLogout() {
        this.logout({
          vm: this,
          confirm: true
        })
      }
    }
  }
</script>